	export default {
		data() {
			return {
      // 控制回到顶部显示与隐藏
				goTopStatus: false
			}
    },
    onLoad() {
      // 获取单品详情信息接口
      this.getDetails()
    },
		//监听页面高度(上滑或者下滑)
		onPageScroll(obj) {
			if (obj.scrollTop > 363) {
				return this.goTopStatus = true
			}
			this.goTopStatus = false
		},
		methods: {
      // 获取单品详情信息接口
      getDetails() {
        this.$http.get('/com/getDetails', { params: { com_id:1 } }).then(res => {
          console.log(res)
        })
      },
      // 回到顶部
      goToTop() {
        uni.pageScrollTo({
					scrollTop: 0
				})
      },
      // 去确定订单页面
      goConfirmOrder() {
        uni.navigateTo({
          url: '/pages/confirmOrder/confirmOrder'
        });
      }
    }
	}
